export class IUser {
  id: number;
  email: string;
  first_name: string;
  last_name: string;
  avatar: string;
}
export class Isupport {
  url: string;
  text: string;
}
export class IPageUser {
  data: IUser[];
  page: number;
  per_page: number;
  support: Isupport;
  total: number;
  total_pages: number;
}
